<template>
  <div
    class="avatar avatar-xl rounded-circle tk-bg-color-card8 tk-position-relative"
  >
    <img
      :src="url"
      v-if="!isNull(url) && imgIsValid"
      @error="imgIsValid = false"
      class="tk-position-absolute"
      style="right: 0"
    />
    <span class="text-xxl">{{ getFirstLetter(name) }}</span>
  </div>
</template>

<script>
export default {
  props: {
    uId: {
      type: String,
      default: "",
    },
    studioId: {
      type: String,
      default: "",
    },
    name: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      url: "",
      imgIsValid: true,
    };
  },
  async mounted() {
    this.refresh();
  },
  watch: {
    uId() {
      this.refresh();
    },
    studioId() {
      this.refresh();
    },
    name() {
      this.refresh();
    },
  },
  methods: {
    isNull(data) {
      return this.$tools.isNull(data);
    },
    getFirstLetter(str) {
      return this.$tools.getFirstLetter(str);
    },
    refresh() {
      let self = this;
      if (!self.isNull(self.studioId)) {
        console.log("加载studio头像");
        self.url =
          "https://firebasestorage.googleapis.com/v0/b/tunekey-2019.appspot.com/o/images%2Fstudio_logos%2F" +
          self.studioId +
          ".jpg?alt=media&token=db73a553-4162-4a94-a1fe-87ca16f38400";
      } else if (!self.isNull(self.uId)) {
        console.log("加载用户头像");
        self.url =
          "https://firebasestorage.googleapis.com/v0/b/tunekey-2019.appspot.com/o/images%2Fuser_avatars%2F" +
          self.uId +
          ".jpg?alt=media&token=42b79c74-4e37-4207-8591-efea10dd794d";
      }

      console.log("头像URL: ", self.url);
    },
  },
};
</script>